<template>
  <div>
    <div class="s-create-tab-btns">
      <mu-raised-button @click="tabClicked(0)" label="Templates" class="s-tab-btn" :class="selectedTab === 0 ? 's-primary-btn' : 's-default-btn'"/>
      <mu-raised-button @click="tabClicked(1)" label="Projects" class="s-tab-btn" :class="selectedTab === 1 || selectedTab === 3 ? 's-primary-btn' : 's-default-btn'"/>
      <mu-raised-button @click="tabClicked(2)" label="Assets" class="s-tab-btn" :class="selectedTab === 2 ? 's-primary-btn' : 's-default-btn'"/>
    </div>
    <div class="s-tab-content">
      <div v-if="selectedTab === 0">
        <mu-grid-list :cols="5" :padding="10" :cellHeight="300">
          <mu-grid-tile v-for="(tile, index) in tempData" :key="index">
            <mu-flat-button class="tem-avatar" @click="handleItemClick(tile, 'template', $event)">
              <img v-if="tile.id !== '-1'" class="tem-avatar" :src="tile.avatar"/>
              <div v-if="tile.id === '-1'" class="blank-template"><div>{{tile.name}}</div></div>
            </mu-flat-button>
          </mu-grid-tile>
        </mu-grid-list>
      </div>
       <div v-if="selectedTab === 1">
         <mu-grid-list :cols="5" :padding="10" :cellHeight="300">
          <mu-grid-tile v-for="(tile, index) in projData" :key="index">
            <mu-flat-button class="tem-avatar" @click="handleItemClick(tile, 'project', $event)">
              <img v-if="tile.id !== '-1'" class="tem-avatar" :src="tile.avatar"/>
              <div class="s-create-proj-name">
                <div>
                  <div class="s-create-proj-name-content">{{tile.name}}</div> 
                </div> 
                </div>
            </mu-flat-button>
          </mu-grid-tile>
        </mu-grid-list>
      </div>
      <div v-if="selectedTab === 2">
         <mu-grid-list :cols="5" :padding="10" :cellHeight="300">
          <mu-grid-tile v-for="(tile, index) in asstsData" :key="index">
            <mu-flat-button class="tem-avatar" @click="handleItemClick(tile, 'asset', $event)">
              <img v-if="tile.id !== '-1'" class="tem-avatar" :src="tile.avatar"/>
              <div v-if="tile.id === '-1'" class="blank-template"><div>{{tile.name}}</div></div>
            </mu-flat-button>
          </mu-grid-tile>
        </mu-grid-list>
      </div>
      <!-- When opening a project -->
      <div v-if="selectedTab === 3">
        <div class="s-create-tab-action-header">
          <mu-flat-button slot="left" class="s-create-tab-action-header-btn-back" @click="backButtonClicked">
            <i class="si-keyboard-arrow-left" style="height: 100%; margin-bottom: 7px;"></i>
          </mu-flat-button>
          <div class="s-create-tab-action-header-btn-title-wrapper">
            <div class="s-create-tab-action-header-btn-title"> Nicca's Creative Lab</div>
          </div>
        </div>
        <div class="s-create-proj-detail-container">
          <mu-grid-list :cols="5" :padding="10" :cellHeight="300">
            <mu-grid-tile v-for="(tile, index) in projDetailData" :key="index">
              <mu-flat-button class="tem-avatar" @click="handleItemClick(tile, 'project', $event, true)">
                <img v-if="tile.id !== '-1'" class="tem-avatar" :src="tile.avatar"/>
                <div class="s-create-proj-name" v-if="tile.id !== '-1'">
                  <div>
                    <div class="s-create-proj-name-content">{{tile.name}}</div>
                    </div>
                  </div>
                <div v-if="tile.id === '-1'" class="blank-template-w">
                  <div>
                    <i class="si-plus" style="font-size: 50px!important;"></i> <br/>NEW FILE
                  </div>
                </div>
              </mu-flat-button>
            </mu-grid-tile>
          </mu-grid-list>
        </div>
      </div>
    </div>
    <!-- Menus -->
    <div ref="templateMenu" v-show="tabType === 'template'"  
      class="tem-action-menu h-100" :class="[tabType === 'template' ? 'tem-action-menu-shown' : 'tem-action-menu-hidden']">
       <mu-list style="padding: 0">
        <mu-list-item title="Create Project" class="tem-action-item" @click="templateMenuCommands(0)"/>
        <mu-divider inset class="temp-action-item-divider"/>
        <mu-list-item title="Edit Template" class="tem-action-item" @click="templateMenuCommands(1)"/>
        <mu-divider inset class="temp-action-item-divider"/>
        <mu-list-item title="Delete Template" class="tem-action-item" @click="templateMenuCommands(2)"/>
      </mu-list>
    </div>
     <div ref="projectMenu" v-show="tabType === 'project'"  
      class="tem-action-menu h-97" :class="[tabType === 'project' ? 'tem-action-menu-shown' : 'tem-action-menu-hidden']">
       <mu-list style="padding: 0">
        <mu-list-item title="Open Project" class="tem-action-item" @click="projectMenuCommands(0)"/>
        <mu-divider inset class="temp-action-item-divider"/>
        <mu-list-item title="Delete Project" class="tem-action-item" @click="projectMenuCommands(1)"/>
      </mu-list>
    </div>
  </div>
</template>
<script>
export default {
  name: "storya-tabs",
  props: ["items"],
  data() {
    return {
      selectedTab: 0, // the current selected tab
      tabType: "", // the type of tan being opened
      tempData: [
        {
          id: "-1",
          name: "Blank Template",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "1",
          name: "Flow",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "2",
          name: "Reflux",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "3",
          name: "Titane",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "4",
          name: "Test 5",
          avatar:
            "data:image/png;base64,"
        }
      ],
      projDetailData: [
        {
          id: "-1",
          name: "New File",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "1",
          name: "Flow",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "2",
          name: "Reflux",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "3",
          name: "Titane",
          avatar:
            "data:image/png;base64,"
        }
      ],
      projData: [
        {
          id: "1",
          name: "Flow",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "2",
          name: "Reflux",
          avatar:
            "data:image/png;base64,"
        },
        {
          id: "3",
          name: "Titane",
          avatar:
            "data:image/png;base64,"
        }
      ],
      asstsData: [
        {
          id: "1",
          name: "Flow",
          avatar:
            "data:image/png;base64,"
        }
      ]
    };
  },
  beforeMount() {
    document.addEventListener("mousedown", this.listenForDocumentClickEvents);
  },
  beforeDestroy() {
    document.removeEventListener(
      "mousedown",
      this.listenForDocumentClickEvents
    );
  },
  methods: {
    tabClicked(val) {
      this.selectedTab = val;
      this.menuType = "";
    },
    handleItemClick(item, type, evnt, isProjectDetail) {
      if (item.id === "-1") {
        return;
      }
      this.tabType = type;
      // set a delay to make sure the menu is rendered
      setTimeout(() => {
        this.$_showMenu(evnt, type);
      }, 50);
    },
    $_getMenu(type) {
      if (this.tabType === "template") {
        return this.$refs.templateMenu;
      } else if (this.tabType === "project") {
        return this.$refs.projectMenu;
      } else {
        return null;
      }
    },
    $_showMenu(evnt, type) {
      if (evnt.target.className === 'mu-ripple-wrapper') {
        var elem = this.$_getMenu();
        var pe = evnt.target;
        pe.appendChild(elem);
      } else {
        this.tabType = '';
      }
    },
    listenForDocumentClickEvents(evt) {
      if (!this.tabType) {
        return;
      }
      var elem = this.$_getMenu();
      if (!evt.target || !elem.contains(evt.target)) {
        this.tabType = "";
      }
    },
    templateMenuCommands(val) {
      this.tabType = "";
    },
    projectMenuCommands(val) {
      console.log(val);
      this.tabType = "";
      if (val === 0) {
        this.selectedTab = 3;
      }
    },
    backButtonClicked() {
      this.selectedTab = 1;
    }
  }
};
</script>
<style scoped>
.h-100 {
  height: 100px !important;
}
.h-97 {
  height: 97px !important;
}
.s-create-proj-detail-container {
  margin-top: 20px;
}
.s-create-tab-action-header {
  display: inline-flex;
  position: relative;
}
.s-create-tab-action-header-btn-back {
  border: 1px solid #333333;
  background-color: transparent;
  border-radius: 0;
  width: 33px;
  min-width: 0;
  position: absolute;
  margin: auto 0;
  bottom: 0;
  top: 0;
}
.s-create-tab-action-header-btn-title-wrapper {
  margin-left: 60px;
  /* border: 1px solid red; */
  text-align: center;
  display: table;
  height: 100%;

}
.s-create-tab-action-header-btn-title {
  font-size: 17px;
  display: table-cell;
}

.s-create-proj-name {
  width: 100%;
  height: 40px;
  font-family: Raleway;
  font-size: 16px;
  background-color: #009d70;
  position: absolute;
  bottom: 0;
  opacity: 0.92;
  text-align: left;
  /* display: table; */
  padding-left: 15px;
}
.s-create-proj-name > :first-child {
  /* display: table;
  vertical-align: middle;
  height: 100%;
  position: absolute;
  margin: 0 auto;
  top: 0;
  bottom: 0; */
  width: 100%;
  height: 100%;
  /* display: table; */
}
.s-create-proj-name-content {
  height: 100%;
  position: absolute;
  margin: auto auto;
  bottom: 0;
  top: 0;
  text-align: center;
  margin-bottom: 4px;
}
.tem-action-menu {
  width: 170px;
  height: 100px;
  position: absolute;
  margin: auto auto;
  bottom: 0;
  top: 0;
}
.s-tab-btns {
  display: flex;
}
.s-tab-content {
  width: 100%;
  height: 100%;
  margin-top: 10px;
  font-family: Raleway;
  overflow: hidden;
}
.s-create-tab-btns > :last-child,
.s-create-tab-btns > :nth-last-child(2) {
  margin-left: -10px;
}
.s-tab-btn {
  width: 170px;
  height: 30px;
  margin-left: 0;
}
.tem-avatar {
  width: 100%;
  height: 100%;
}
.mu-grid-tile-titlebar {
  display: none !important;
}
.mu-flat-button {
  line-height: inherit;
  border-radius: 0;
}
.blank-template {
  color: #000;
  /* text-align: center;  */
  background-color: #fff;
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-weight: bold;
  color: #111111;
  display: table;
}
.blank-template > :first-child {
  display: table-cell;
  vertical-align: middle;
}
.blank-template-w {
  color: #fff !important;
  /* text-align: center;  */
  background-color: #000;
  border: 2px solid #333333;
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-weight: bold;
  color: #111111;
  display: table;
}
.blank-template-w > :first-child {
  display: table-cell;
  vertical-align: middle;
}
</style>



