<template>
  <div>
    <div class="list-container" >
    <mu-grid-list :cols="2" :padding="10" >
      <mu-grid-tile :cols="1" v-for="(tile, index) in sampleItems" :key="index" :id="tile.id">
         <mu-flat-button class="tem-avatar" @click.stop="templateSelectionChanged(tile,$event)" >
          <img class="tem-avatar" :src="tile.avatar"/>
         </mu-flat-button>
      </mu-grid-tile>
      
    </mu-grid-list>
    <!-- Popup list -->
    <div ref="tempPopupMenu" class="tem-action-menu" :class="[showMenu ? 'tem-action-menu-shown' : 'tem-action-menu-hidden']">
       <mu-list style="padding: 0">
        <mu-list-item title="Preview" class="tem-action-item" @click.stop="onPreview($event)"/>
        <mu-divider inset class="temp-action-item-divider"/>
        <mu-list-item title="Apply" class="tem-action-item" @click.stop="onApply($event)"/>
      </mu-list>
    </div>
  </div>
    <mu-dialog dialog-class="dialog-width" :open="dialogOpen" @close="dialogOpen = false" :title="onApplys === false ? selectedTemplate.name : ''" scrollable>
      <div v-if="onApplys" class="dialog-title">
        <p> Your are about to apply hits template to your current project.All changes will be discarded.
        </p>
      </div>
       <mu-row gutter>
            <mu-col width="50" desktop class="dialog-col">
              <img class="tem-avatar" src="data:image/png;base64,"/>
            </mu-col>
          </mu-row>
      <br>
      <mu-row gutter style="padding-bottom:5px">
        <mu-raised-button label="Close" class="dialog-btn-width" @click.stop="dialogOpen = false"/>
        <mu-raised-button label="Proceed" class="dialog-btn-width" @click.stop="dialogOpen = false"/>
      </mu-row>
       <mu-row gutter>
          <mu-checkbox name="group" nativeValue="" label="Do not show this again" class="demo-checkbox"/>
       </mu-row>
      <!-- <mu-flat-button primary label="Close" @click="dialogOpen = false" slot="actions"/> -->
    </mu-dialog>
  </div>
</template>
<script>
/*
 * @component: select-template
 * @events:
 *  templateSelectionChanged: occurs when selecting a new template
 *  onApply: occurs when the apply button is clicked
 *  onPreview: occurs when the preview button is clicked
*/
export default {
  name: 'select-template',
  props: ['items'],
  beforeMount() {
    document.addEventListener('mousedown', this.listenForDocumentClickEvents)
  },
  beforeDestroy() {
     document.removeEventListener('mousedown', this.listenForDocumentClickEvents)
  },
  mounted () {
    let grid = document.getElementsByClassName('list-container')
    this.gridElements = grid[0].children[0].children
    console.log('grid',grid[0].children)
    
    
  },
  data() {
    return {
      dialogOpen: false,
      selectedTemplate: {},
      showMenu: false,
      openMenu: false,
      currentElement: null,
      onApplys: false,
      sampleItems: [
        {
          'id': '1',
          'name': 'Flow',
          'avatar': "data:image/png;base64,"
        },
        {
          'id': '2',
          'name': 'Reflux',
          'avatar': "data:image/png;base64,"
        },
        {
          'id': '3',
          'name': 'Titane',
          'avatar': "data:image/png;base64,"
        }
      ],
      gridElements: []
    }
  },
  methods: {
    templateSelectionChanged(item,event) {
      this.currentElement = event
      this.selectedTemplate = item;
      this.showMenu = this.selectedTemplate != null;
      this.removeSelectedfn(item,event)
      this.$emit('templateSelectionChanged', item);
    },
    onPreview(event) {
      this.currentElement.target.removeAttribute('style')
      this.onApplys = false
       this.dialogOpen = true;
       this.showMenu = true;
       this.$emit('onPreview', this.selectedTemplate);
    },
    onApply() {
      this.currentElement.target.removeAttribute('style')
      this.onApplys = true
      this.dialogOpen = true;
      this.showMenu = true;
       this.$emit('onApply', this.selectedTemplate);
    },
    listenForDocumentClickEvents(evt) {
      if (evt && !evt.target || !this.$refs.tempPopupMenu.contains(evt.target)) {
        if (this.showMenu && !this.dialogOpen) {
          this.selectedTemplate = {};
        }else{
        this.showMenu = false
        }
      }
    },
    removeSelectedfn (item, event) {
      if(!this.dialogOpen){
        event.stopPropagation()
        let popover = document.getElementsByClassName('tem-action-menu')[0]
          for(let i = 0; this.gridElements.length > i; i++){
            if(this.gridElements[i].id === item.id){
              this.currentElement.srcElement.style.background = 'rgba(0, 0, 0, 0.29)'
              this.currentElement.target.appendChild(popover)
            }else{
            this.gridElements[i].getElementsByClassName('mu-ripple-wrapper')[0].removeAttribute('style')
            }
        }
      }
    }
  }
}
</script>
<style>
.list-container {
  margin: -16px;
  background-color: #111111;
  padding-right: 10px;
}
.mu-grid-tile-titlebar {
  display: none!important
}
.tem-avatar {
  width: 100%;
  height: 100%;
}
.mu-item-wrapper.hover {
  background-color: transparent;
}
.mu-item-wrapper.overlays {
  background: rgba(0, 0, 0, 0.29);
}
.dialog-title{
      position: relative;
    width: auto;
    /* width: 675px; */
    text-align: center;
    font-size: 1.3em;
    color: #ffffff;
}
.dialog-width {
  width: 50%;
  background-color: #1A1A1A;
}
.dialog-btn-width{
  width: 45%;
 border: 1px rgba(255, 255, 255, 0.13) solid;
  background-color: #1A1A1A;
}
.dialog-col{
  margin: 0 auto;
}
.mu-raised-button.hover{
  background-color: #009D70;
}
</style>





